import { Component, createResource, For, Show } from 'solid-js';

import BasicModal from '@/components/modal/BasicModal';
import SafeLink from '@/components/utils/SafeLink';
import resolveAsset from '@/utils/resolveAsset';

type AboutProps = {
  onClose: () => void;
};

type PackageInfo = {
  self: {
    name: string;
    author: string;
    version: string;
    homepage: string;
    licenseSpdx: string;
    licenseText: string;
  };
  packages: {
    name: string;
    version: string;
    licenseSpdx: string;
    licenseText: string;
  }[];
};

const fetchPackageInfo = async (): Promise<PackageInfo> => {
  const res = await fetch(resolveAsset('packageInfo.json'));
  const body = await res.text();
  return JSON.parse(body) as PackageInfo;
};

const commit = import.meta.env.VITE_COMMIT as string | null;

const About: Component<AboutProps> = (props) => {
  const [packageInfo] = createResource(fetchPackageInfo);

  return (
    <BasicModal onClose={props.onClose}>
      <div class="p-8">
        <div class="flex flex-col items-center pt-8">
          <img src={resolveAsset('images/rabbit_app_256.png')} alt="Logo" width="64" height="64" />

          <h1 class="my-4">
            Rabbit{' '}
            <span id="app-version">
              v{packageInfo()?.self?.version}
              <Show when={commit}> ({commit})</Show>
            </span>
          </h1>
        </div>

        <h2 class="my-4 text-xl font-bold"></h2>

        <p class="my-4">
          
          <a
            class="text-blue-500 underline"
            href="https://github.com/syusui-s/rabbit/issues/new/choose"
            target="_blank"
            rel="noopener noreferrer"
          >
            GitHubIssues
          </a>
          
        </p>

        <h2 class="my-4 text-xl font-bold"></h2>

        <p class="my-4">
          
          <SafeLink class="text-blue-400 underline" href="https://github.com/syusui-s/rabbit">
            GitHub
          </SafeLink>
          
        </p>

        <h2 class="my-4 text-xl font-bold"></h2>

        <p class="my-4">
          Copyright (C) 2023 Shusui Moyatani and{' '}
          <SafeLink
            class="text-blue-400 underline"
            href="https://github.com/syusui-s/rabbit/graphs/contributors"
          >
            Rabbit contributors
          </SafeLink>
        </p>

        <p class="my-4">
          
          GNU3()
          
        </p>

        <p class="my-4">
          
          <strong class="font-bold"></strong><em></em>
          <em></em> 
          GNU
        </p>

        <p class="my-4">
          GNU
          
          <a class="link" href="https://www.gnu.org/licenses/">
            https://www.gnu.org/licenses/
          </a>
          
        </p>

        <a class="text-blue-500 underline" href="https://gpl.mhatta.org/agpl.ja.html">
          
        </a>

        <pre class="max-h-96 overflow-scroll rounded bg-zinc-100 p-4 text-xs">
          {packageInfo()?.self.licenseText}
        </pre>

        <h2 class="my-4 text-xl font-bold"></h2>

        <For each={packageInfo()?.packages ?? []} fallback="">
          {(p) => (
            <>
              <h3 class="mb-2 mt-4 font-mono">
                {p.name}@{p.version} ({p.licenseSpdx})
              </h3>
              <pre class="max-h-96 overflow-scroll rounded bg-zinc-100 p-4 text-xs">
                {p.licenseText}
              </pre>
            </>
          )}
        </For>
      </div>
    </BasicModal>
  );
};

export default About;
